import React from 'react'
import ListItem from './ListItem';
export default class ShoppingList extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            initialItems: [
                "Apples",
                "Broccoli",
                "Chicken",
                "Bacon",
                "Eggs",
                "Salmon",
                "Granola",
                "Bananas",
                "Beer",
                "Wine",
                "Yogurt"
            ],
            items: [],
            searchTerm: ''    
        }
    }
    handleChange = (e) => {
        this.setState({
            searchTerm: e.target.value
        })
    }
    filterList = (e) => {
        e.preventDefault();
        let updatedList = this.state.initialItems;        
        updatedList = updatedList.filter( item => {
            return item.toLowerCase().startsWith(this.state.searchTerm.toLowerCase()) === true //!== -1;    
        });
        this.setState({items: updatedList});
    }
    componentDidMount(){
        this.setState({items: this.state.initialItems})
    }
    render() {
        return (
            <div className="filter-list">
                <nav className="navbar navbar-light bg-light">
                    <form className="form-inline" onSubmit={this.filterList}>
                        <input className="form-control mr-sm-2" type="search" placeholder="Search" value={this.state.searchTerm} onChange={this.handleChange} />
                        {/* <input className="form-control mr-sm-2" type="search" placeholder="Search" onInput={this.filterList} /> */}
                        {/* <button className="btn btn-outline-success my-2 my-sm-0" type="button" onClick={this.filterPlanets}>Search</button> */}
                    </form>
                </nav> 
                <ListItem items={this.state.items}/>
                <br />
                <p>Search term: { this.state.searchTerm }</p>
                
            </div>  
        )
    }
}

